@import url(https://fonts.googleapis.com/css?family=Titillium+Web:400,700i&display=swap);
body {
  margin: 0 auto;
  padding: 0;
  font-family:'Titillium Web', sans-serif; 
  /* background: linear-gradient(to left, rgb(194, 161, 240) 0%, rgb(249, 243, 251) 100%); */
  background-color: rgba(223, 203, 250, 0.557);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Accent Color: rgb(223, 223, 223); */
div{
    color: white;
}

h2{
    margin: 0;
}

.bg{
    background-color: rgb(219, 196, 255);
    padding: .5rem;
    margin: 0 auto;
    border-radius: 1rem 1rem 0 0;
}

hr{
    display: block; 
    height: 1px;
    border: 0; 
    border-top: 2px solid rgb(255, 255, 255);
    margin-top: 0; 
    margin-bottom: 1em;
    padding: 0;
    border-radius: 1rem;
}

.btn-slot{
    /* border-radius: 1rem; */
    background-color:  rgb(219, 196, 255);
    width: 100%; /*75.5 8em */
    height: 5em;
    color: white;
    font-size: 1.1em;
    text-align: center;
}

.btn-design{
    text-align: center;
    /* //For square design 
    display: inline-block;
    padding: 15px;
    margin: 20px;  */
} 

.btn-slot:hover{
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    -webkit-transition: 1s;
    transition: 1s;
}
.btn-container{
    padding-bottom: 5rem;
}
#modal-cont{
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    border: 2px solid grey;
    background-color: rgba(223, 203, 250, 0.557);
    border-radius: 5px;  
    text-align: center;
    padding: 15px;  
    width: 30%;
}
.close{
    /* width: 30%; */
    text-align: right;
    cursor: pointer;
    background: none;
    border: none;
    color: white;
    font-size: 2em;
}
.label-cont{
    display: flex;
    flex-direction: column;
    padding: 2em 5em;
}
.mod{
    display: flex;
    text-align: left;
    /* text-shadow: 1px 1px 2px grey; */
}

.field-required{
    color: red;
}

.w3-input:active {
    box-shadow: 2px rgb(219, 196, 255);
}

input[type=text], input[type=tel] {
    border: none;
    border-radius: none;
    border-bottom: 2px solid rgb(219, 196, 255);
    border-bottom-style: line ;
    background-color: transparent;
    padding: 5px;
    /* text-shadow: 1px 1px 2px grey; */
    width: 100%;
    color:white;
} 

input[type=submit]{
    margin-top: 2em;
    width: 100px;
    height: 35px;
    border-radius: 5px;
    color: white;
    background-color:rgb(219, 196, 255);
    /* box-shadow: 1px 1px 2px 2px white; */
}

input:focus{
    /* box-shadow: 0 -1px 1px rgb(219, 196, 255); */
    outline: none;
}

::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
}

::-moz-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
}

::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: white;
    opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: white;
}

::-ms-input-placeholder { /* Microsoft Edge */
    color: white;
}
.body{
  margin: 1.5rem 2rem;
}

.title{
  color:white;
  text-align: center;
  text-shadow: 2px 1px 2px rgb(175, 118, 199),
               2px 1px 1px rgb(255, 255, 255),
               1px 2px 2px black;
  padding-bottom: 30px;
}

